const timeNotation = (temp) => {
    var time;
/*
    switch(temp[5]){
        case 'P': time='ì˜¤í›„ '; break;
        default: time='ì˜¤ì „ ';
    }
    switch(temp[0]){
        case '0': time += temp.slice(1, 5); break;
        default: time += temp.slice(0, 5)
    }
*/

    if (temp.length > 5){
        switch(temp[5]){
            case 'P': time='ì˜¤í›„ '; break;
            default: time='ì˜¤ì „ ';
        }
        switch(temp[0]){
            case '0': time += temp.slice(1, 5); break;
            default: time += temp.slice(0, 5)
        }
    }
    else 
    {
        var hh = Number(temp.slice(0, 2));
        var mm = temp.slice(2);
        if ( hh > 12 ) {
            time = 'ì˜¤í›„ '+(hh-12)+mm;
        }
        else if ( hh == 12 ){ 
            time = 'ì˜¤í›„ 12'+mm;
        }
        else if ( hh > 0) {
            time = 'ì˜¤ì „ '+hh+mm;
        }
        else {
            time = 'ì˜¤ì „ 12'+mm;
        }
    }

    return time;
    
};

export default timeNotation;